.App {
  display: flex;
  flex: 1;
  flex-direction: column;
  padding-top: 20px;
}

.inputcontainer{
  display: flex;
  width: 340px;
  height: 73px;
  margin-left: 25px;
  flex-direction: row;
  align-items: center;
  border-bottom: 1px solid #203261;
}
.titlecontainer{
  width:80px;
}
.titlecontainer span{
  font-size: 16px;
}
.language{
  position: absolute;
  top:25px;
  right: 25px;
}
.inputitem{
  width:256px;
  /* height: 73px; */
  margin-left: 10px;

  outline: 0;
  border-width: 0 0 0px;
  border-color:  #203261;

  font-size: 22px;
}
.button{
  width:336px;
  height: 42px;
  margin-top: 20px;
  background-color: #203261;
  border-radius: 20px;
  display: flex;
  align-self: center;
  align-items: center;
  justify-content: center;
  border-width: 0;
}
.vcodeinput{
  position: relative;
  display: flex;
  justify-content: center;
  width: 340px;
  height: 73px;
  margin-left: 13px;
  /* border: 1px solid blue; */


}
.vcodebtn {
  position: absolute;
  right: 0px;
  top:5px;
  width: 100px;
  height: 63px;
  display: flex;
  background-color: #203261;
  justify-content: center;
  align-items: center;
}
.vcodetext{
  color: #fff;
  font-size: 16px;
}
.button span{
  color:#fff;
  font-size: 16px;
}
a:link {color: #203261}
a:visited {color: #203261}
a:hover {color: #203261}
a:active {color: #203261}

.link{
  width: 100%;
  border-width: 1px;
  border-color: red;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;

}
.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
